.icons-list {
    list-style: none;
    margin: 0 -1px -1px 0;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    >li {
        -ms-flex: 1 0 3rem;
        flex: 1 0 3rem;
    }
}

.icons-list-wrap {
    overflow: hidden;
}

.icons-list-item {
    text-align: center;
    height: 3rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
    border: 1px solid #e9ebfa;
    margin: 4px;
    border-radius: 50px;
    // background: #f6f6fb;
    i {
        font-size: 1.05rem;
        color: #656585;
    }
}


